:root {
  --purple: hsl(259, 100%, 65%);
  --light-red: hsl(0, 100%, 67%);
  --white: hsl(0, 0%, 100%);
  --off-white: hsl(0, 0%, 94%);
  --light-grey: hsl(0, 0%, 86%);
  --smokey-grey: hsl(0, 1%, 44%);
  --off-black: hsl(0, 0%, 8%);
}

:root {
  --font-poppins: "Poppins", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

body {
  background-color: var(--off-white);
  font-family: var(--font-poppins);
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

h2 {
  font-size: 2.5rem;
}
@media (min-width: 47.9375em) {
  h2 {
    font-size: 3.75rem;
  }
}

p {
  font-size: 2rem;
}

.container {
  background: var(--white);
  border-radius: 5% 5% 25% 5%;
  height: 31.25rem;
  margin: 5rem auto 0;
  max-width: 50rem;
  width: 90vw;
}
@media (min-width: 47.9375em) {
  .container {
    margin-top: 20vh;
  }
}

.flex-row {
  display: flex;
  flex-direction: row;
}

.flex-column {
  display: flex;
  flex-direction: column;
}